.saved-file-view {
  margin: auto;
}
.savedFiles {
  width: 300px;
  /* height: 350px; */
  padding: 1rem;
  overflow-y: scroll;
  border-radius: 2px;
  border-radius: 10px;
  font-family: 'Lato';
  margin: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}
.savedFiles::-webkit-scrollbar {
  width: 8px; /* width of the entire scrollbar */
}
.savedFiles::-webkit-scrollbar-track {
  background: #f1f1f1; /* color of the tracking area */
}
.savedFiles::-webkit-scrollbar-thumb {
  background-color: #c1c1c1; /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 3px solid #f1f1f1; /* creates padding around scroll thumb */
}
.fileSection {
  max-width: 300px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(75px, 1fr));
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}
