  background: rgba(255, 255, 255, 0.4);
  border: 0.8px solid rgba(153, 153, 153, 0.2);
  box-sizing: border-box;
  border-radius: 3px;
  margin: 0 12px;
  outline: none;
  padding: 0 10px;
  color: #999999;
}
.search-input::-webkit-input-placeholder{
  color: #999999;
}
.search-input:-ms-input-placeholder{
  color: #999999;
}
.search-input::placeholder{
  color: #999999;
}

.search-icon {
  width: 18px;
  height: 18px;
}

.search-avatar {
  width: 39px;
  height: 39px;
  margin: 0 12px;
}
.zoom-component-after {
  width: 196px;
  height: 48px;
  position: absolute;
  right: 42px;
  bottom: 62px;
  background: #ffffff;
  border: 1px solid #00b87c;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.zoom-percentage,
.zoom-out,
.zoom-in,
.zoom-reset {
  display: inline-block;
}
.zoom-percentage,
.zoom-reset {
  font-style: normal;
  font-weight: normal;
  font-family: "Lato", sans-serif;
  font-size: 11px;
}
.zoom-percentage {
  min-height: 28px;
  min-width: 28px;
  color: #333333;
  margin-right: 36px;
  text-align: center;
  line-height: 28px;
}
.zoom-out {
  margin-right: 11.33px;
}
.zoom-in,
.zoom-out {
  border: none;
  background: none;
  min-width: 14px;
  min-height: 14px;
  position: relative;
  cursor: pointer;
}
.zoom-in svg,
.zoom-out svg {
  position: absolute;
  left: 5%;
  top: 5%;
}
.zoom-in:hover svg path,
.zoom-out:hover svg path {
  fill: #00b87c;
}
.zoom-reset {
  min-width: 51px;
  min-height: 20px;
  border: 0.5px solid #e1e1e1;
  box-sizing: border-box;
  border-radius: 4px;
  color: #00b87c;
  background: none;
  text-align: center;
  margin-left: 11.12px;
  cursor: pointer;
}
.zoom-component-before {
  cursor: pointer;
  width: 48px;
  height: 48px;
  position: absolute;
  right: 42px;
  bottom: 62px;
  background: #00b87c;
  border-radius: 10px;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 11px;
  font-style: normal;
  font-weight: normal;
  font-family: "Lato", sans-serif;
  border: none;
}
.Invisible {
  height: 100vh;
  left: 0;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 0;
}
@media only screen and (max-width: 768px) {
  .zoom {
    display: none;
  }
}

